<template>
  <div class="web-footer">
    <div class="footer">
      <div class="campus-qrcode">
        <img src="../../assets/fans/campus-qrcode.jpg"/>
      </div>
      <div style="float:left;overflow:hidden;padding-left:0.2rem;margin-top: .36rem">
        <div class="container">
          <div class="container-footer">
            <div>
              <span v-for="i in footers" class="footer-item" style="font-size: 12px">
                <a target="_blank" :href="i.href">{{i.name}}</a>
              </span>
            </div>
            <div style="margin-top: 0.05rem">
              <span v-for="i in footers1" class="footer-item" style="font-size: 12px">
                <a target="_blank" :href="i.href">{{i.name}}</a>
              </span>
            </div>
          </div>
        </div>
        <div class="link-method container" style="padding: 0;font-size: 12px;text-align: left">
          <span style="padding-left: 0.08rem">邮箱:
            <a href="mailto:campusmaster@baidu.com">campusmaster@baidu.com</a>
          </span>
          <div style="padding-left: 0.08rem" class="text">欢迎关注百度校园微信公众号</div>
        </div>
      </div>
    </div>
    <div class="copyright" style="font-size:12px">
      ©2019 Baidu
      <a href="https://www.baidu.com/duty/">使用百度前必读</a>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'Footer',
    data() {
      return {
        footers: [{
          'name': '关于百度',
          'type': 'link',
          'href': 'http://home.baidu.com'
        }, {
          'name': 'About Baidu',
          'type': 'link',
          'href': 'http://ir.baidu.com'
        }, {
          'name': '校园微博',
          'type': 'link',
          'href': 'http://weibo.com/baiduxiaoyuan'
        }, {
          'name': '百度招聘',
          'type': 'link',
          'href': 'https://talent.baidu.com/external/baidu/index.html'
        }],
        footers1: [{
          'name': '校园大使',
          'type': 'link',
          'href': 'http://dashi.baidu.com/'
        }, {
          'name': '百赞平台',
          'type': 'link',
          'href': 'http://zan.baidu.com/'
        }]
      }
    }
  }
</script>
<style lang="stylus" scoped>
  @import "../../assets/icon/font.css"
  .web-footer {
    font-family 'fzzy' !important
    background-color: #000;
    padding-bottom .4rem
    .footer {
      padding: .4rem
      color: #fff;
      text-align: center;
      overflow: hidden
      .container-footer {
        >div{
          overflow hidden
        }
        span {
          cursor: pointer;
          float left
        }
      }
      .footer-item {
        list-style: none;
        /*margin:0 0 0.2rem 0;*/
        padding: 0 0.08rem;
        text-align: center;
        border-right: 1px solid #fff;
        &:last-child {
          border: none;
        }
        a {
          color: #fff;
          text-decoration: none;
          &:hover, &:focus, &:active {
            color: #fff;
          }
        }
      }
      .link-method {
        text-align: center;
        span {
          margin: 0 0rem;
          a {
            color: #fff;
            text-decoration: none;
            &:hover, &:focus, &:active {
              color: #fff;
            }
          }
        }
      }
      .campus-qrcode {
        margin-top: 0.2rem;
        float: left;
        img {
          height 1.5rem
          width: 1.5rem;
          display block
        }
      }
    }
    .copyright {
      color: #777;
      margin 0 auto
      font-size: 0.12rem;
      text-align center
      a {
        color: #777;
      }
    }
  }
</style>



