<template>
  <div class="content">
    <header></header>
    <article>
      <div>
        <p>{{activity.fir}}</p>
        <p>{{activity.sec}}</p>
      </div>
      <span></span>
    </article>
    <section class="past-area">
      <div class="past-review">
        <div class="past-bg"></div>
        <div class="left elipse"></div>
        <div class="right elipse"></div>
      </div>
      <p>学生们来百度参观并参加各种活动</p>
      <swiper :options="swiperOption" ref="mySwiper">
        <!-- slides -->
        <swiper-slide v-for="item,index in wraps" :key="index">
          <div class='wrap'
               :style="{backgroundImage: 'url(' + item + ')',backgroundSize:'cover',backgroundRepeat:'no-repeat',backgroundPosition:'center center'}"></div>
        </swiper-slide>
        <!-- Optional controls -->
        <!--<div class="swiper-pagination"  slot="pagination"></div>-->
        <!--<div class="swiper-button-prev" slot="button-prev"></div>-->
        <!--<div class="swiper-button-next" slot="button-next"></div>-->
      </swiper>
    </section>
    <section class="join-area">
      <div class="join-bg"></div>
      <ul>
        <li class="join-item" v-for="item,index in joinInfo" :key="index">
          <a href="javascript:;">
            <img :src="item.img" alt="">
            <div>
              <p>{{item.content}}</p>
            </div>
          </a>
        </li>
      </ul>
    </section>
    <div class="title">
      <div class="line"></div>
      <span>活动规则</span>
    </div>
    <div class="rule-content">
      <p v-for="item in rules">{{item}}</p>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
  import 'swiper/dist/css/swiper.css'
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  import swiper1 from '../../assets/fans/lunbo-1.png'
  import swiper2 from '../../assets/fans/lunbo-2.jpg'
  import YueDuDaKa from '../../assets/fans/yuedu-daka.png'
  import QiTaFangShi from '../../assets/fans/qita-fangshi.png'
  import XiaoYuanJinLi from '../../assets/fans/xiaoyuan-jinli.png'
  import SheTuanYuanMeng from '../../assets/fans/shetuan-yuanmeng.png'
  import Footer from './Footer'

  export default {
    name: "Index",
    data() {
      return {
        activity: {
          fir: '百度校园夏季游学之旅从2015年至今已连续举办四年，为来自全国各地的同学们提供了一个互相交流、互相学习、展示自我、共同进步的平台。截至目前，累计已有400多名在校学生参与。',
          sec: '2019年活动主题——“纵青春·AI自由”，将于8月中旬在北京（暂定）举行游学启动仪式。全程免费包吃包住包玩！欢迎全国各地的同学们踊跃报名参与！'
        },
        joinInfo: [
          {
            title: '阅读打卡',
            img: YueDuDaKa,
            content: '从即日起参与百度校园微信公众号阅读打卡活动，累计打卡次数最高的前20名同学即可获得游学专属邀请函，点击菜单栏“阅读打卡”，了解活动规则。'
          },
          {title: '社团圆梦', img: SheTuanYuanMeng, content: '参与百度校园社团圆梦系列活动的社团团长，活动贡献值排名前3的，即可获得游学专属邀请函。'},
          {title: '校园锦鲤', img: XiaoYuanJinLi, content: '在百度校园微信公众号回复“报名”参与活动，我们将从所有成功报名的粉丝中，抽取10名锦鲤送上游学专属邀请函。'},
          {title: '其他方式', img: QiTaFangShi, content: '校园君将根据报名的情况，后续增加新的参与方式和活动名额，新增方式将公布在百度校园微信公众号推文内。'},
        ],
        rules:[
          '1、游学专属邀请函获得者须为在校学生；',
          '2、如有任何问题，详询QQ群：910620799；',
          '3、以上活动中奖名单将于7月中下旬在百度校园微信公众号公布；',
          '4、本活动最终解释权在法律法规范围内归百度校园所有。'
        ],
        swiperOption: {
          autoplay: true,
          loop: true,
          // navigation:{
          //   nextEl:'.swiper-button-next',
          //   prevEl:'.swiper-button-prev'
          // },
          // pagination: {
          //   el: '.swiper-pagination',
          //   clickable :true
          // }
        },
        wraps: [swiper1, swiper2]
      }
    },
    mounted() {

    },
    methods: {},
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper;
      }
    },
    components: {
      swiper,
      swiperSlide,
      Footer
    }
  }
</script>

<style scoped lang="stylus">
  @import "../../assets/icon/font.css"
  .content {
    font-family 'fzzy' !important
    color #262626;
    header {
      width 100%
      height 2.4rem
      background url("../../assets/fans/fensijie-banner.png") no-repeat center center
      background-size cover
    }
    article {
      padding 0.4rem
      height auto
      margin-top .2rem
      position relative
      div {
        box-shadow 0px 0px 20px 6px rgba(0, 0, 0, 0.1);
        padding .55rem .28rem
        line-height .35rem
        p {
          font-size .25rem
          text-align justify
          font-weight normal
          text-indent 2em
        }
        p:nth-child(2) {
          margin-top .4rem
        }
      }
      span {
        display block
        width 3.23rem
        height .51rem
        background url("../../assets/fans/activity-intr.png") center center no-repeat
        background-size cover
        position absolute
        left .1rem
        top .1rem
      }
    }
    .past-area {
      .past-review {
        width 100%
        height .98rem
        overflow hidden
        position relative
        .past-bg {
          width 3.87rem
          height .51rem
          background url("../../assets/fans/past-review.png") no-repeat center center
          background-size cover
          margin 0.23rem auto
        }
        .elipse {
          background #5796f7
          position: absolute
          border-radius 30%
        }
        .left {
          height .3rem
          width .5rem
          top 0
          left -.25rem
        }
        .right {
          height .14rem
          width .36rem
          right -.18rem
          bottom 0
        }
      }
      p {
        font-size .25rem
        text-align center
        padding .25rem 0
      }
      .wrap {
        width 100%
        height 2.5rem
      }
    }
    .join-area {
      padding 0 .4rem;
      overflow hidden
      .join-bg {
        width 3.23rem
        height .51rem
        background url("../../assets/fans/canyu-fangshi.png") center center no-repeat
        background-size cover
        margin .3rem 0
      }
      ul {
        .join-item {
          height 1.6rem
          margin-bottom .3rem
          overflow: hidden
          box-shadow 0px 0px 4px 3px rgba(0, 0, 0, 0.1)
          cursor pointer
          img {
            width 1.6rem
            height 1.6rem
            display block
            float left
          }
          div {
            width 4.6rem
            overflow hidden
            padding .2rem .2rem
            float left
            p {
              font-size .25rem
              text-align justify
              line-height .32rem
            }
          }
          a {
            color #262626
          }
        }
      }
    }
    .title {
      width 6.5rem
      height 0.64rem;
      font-weight 600
      font-size 0.35rem
      text-align center
      color #1c68ef
      margin 0 auto
      margin-top 0.5rem
      overflow: hidden
      position relative
      span {
        width 1.55rem
        height .4rem
        font-weight 600
        display block
        position absolute
        top 0
        bottom 0
        right 0
        left 0
        margin auto
        line-height .4rem
        z-index: 9
        background-color #ffffff
      }
      .line {
        width 100%
        height 2px
        border-bottom 1px dashed #1c68ef
        position absolute
        top 0
        bottom 0
        margin-top auto
        margin-bottom auto
        z-index: 8
      }
    }
    .rule-content {
      width 6.6rem
      height auto
      margin 0 auto
      p {
        font-size 0.25rem;
        margin-bottom: 0.15rem;
        color #262626
        line-height .35rem
      }
    }
  }
</style>



